.c {
  height: 100%;
  position: relative;
  display: flex;
    width:100%;
    background-color: rgb(254, 254, 249);
  
}

.c-type{
  background-color: black;
  color:white;
  position: relative;
  padding-top: 12px !important;
  font-size:17px;
  text-align: center;
  padding-bottom: 12px;
}
.c-bg {
  width: 20px;
  height: 100%;
  background-color:  rgb(201, 199, 199);
  position: absolute;
}

.c-wrapper {
  padding:50px;
  display: flex;
}
.feedback{
  height: 40px;
}
.c-left {
  margin-right: 5% !important;
  flex: 1;
  margin-top: 0px;
}

.c-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.c-title {
  margin-top: 20px !important;
  font-size: 60px;
  width: 100%;
}
.c-title1 {
  /* margin-left: 0px !important; */
  /* margin-top: 0px !important;
  margin-bottom: 10px !important; */
  font-size: 33px;
  width: 100%;
  text-decoration: underline;
}

.c-info-item {
  display: flex;
  align-items: center;
  margin: 40px 0px;
  font-weight: 300;
  width: 70%;
}

.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.c-desc {
  font-weight: 200;
}

form {
  margin-top: 20px;
}

input {
  width: 48%;
  height: 50px;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}

textarea {
  width: 97%;
  margin: 10px 0px;
  font-size: 20px;
  padding-left: 10px;
  height: 200px;
  margin-top: 20px;
  padding-top: 10px;
  /* border-radius: 20px; */
}

button {
  border: none;
  padding: 15px;
  /* background-color: rgb(201, 199, 199); */
  background-color:  rgb(201, 199, 199);
  color:black;
  font-weight: 500;
  cursor: pointer;
}


@media screen and (max-width: 480px) {
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;
  }

  .c-title {
    font-size: 30px;
  }

  .c-info-item {
    margin: 20px 0px;
    width: 100%;
  }
textarea{
  width: 100% !important;
}
  .c-desc {
    display: none;
  }

  form {
    margin-top: 0px;
    display: flex;
    flex-wrap: wrap;
  }

  input {
    width: 35%;
    height: 40px;
    margin: 10px;
    margin-left: 0;
  }

  button {
    margin-top: 10px;
  }
}
