.pl {
  display: flex;
  flex-direction: column;
  width:100%;
  align-items: center;
  text-align: center;
}
.hello{
  box-shadow: 10px 15px 20px #333;
  visibility: hidden !important;
}
.hello img{
height: 0%;
width: 0%;
}
.pl-texts {
  background-image: url(./../../img/th\ \(4\).jfif);
  background-repeat: no-repeat;
  max-width: 100%;
  height: auto;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-size:25px;
  font-family: Georgia, 'Times New Roman', Times, serif;
}
.pl-texts1 {
  background-color:rgb(228, 250, 198);
}
.pl-texts2 {
   background-color:rgb(49, 110, 2);
}
.pl-texts3 {
   /* background-color:black; */
   background-color:rgb(241, 249, 228);
   width: 100%!important;
}
.pl-title {
  text-decoration:underline;
  margin-right: 5% !important;
  margin-left: 5% !important;
  font-size: 50px;
  font-weight: 600;
  margin-top: 25px!important;
  margin-bottom: 15px!important;
  font-family:cursive;
}
.pl-title1 {
  color:rgb(45, 36, 4);
  font-size: 40px;
  font-weight: 600;
  font-family:cursive;
  margin-top: 25px !important;
}
.pl-title2 {
  font-size: 40px;
  font-weight: 600;
  font-family:cursive;
  color:rgb(242, 253, 225);
  margin-top: 25px !important;
}
.pl-title3{
  font-size: 40px;
  font-weight: 600;
  font-family:cursive;
  color:rgb(45, 36, 4);
  margin-top: 25px !important;
}
.pl-title4 {
  font-size: 40px;
  font-weight: 600;
  font-family:cursive;
  color:rgb(45, 36, 4);
  margin-top: 25px !important;
}
/* .i-title-wrapper {
  height: 100%;
} */


@keyframes move1 {
  10% {
    transform: translateX(-15%);
  }
  20% {
    transform: translateX(-35%);
  }
  30% {
    transform: translateX(-45%);
  }
  40% {
    transform: translateX(-60%);
  }
  50% {
    transform: translateX(-75%);
  }
  60% {
    transform: translateX(15%);
  }
  70% {
    transform: translateX(30%);
  }
  80% {
    transform: translateX(45%);
  }
  90% {
    transform: translateX(60%);
  }
  100% {
    transform: translateX(75%);
  }
  
}
.pl-title4{
  animation: move1 4s ease-in-out infinite alternate;
  /* width: 100%; */
  border: 5px solid green;
padding-left: 10px;
padding-right: 10px;
padding-top: 10px;
padding-bottom: 10px;
box-shadow:10px 15px 20px #333;
  font-size: 25px;
  font-weight: 600;
  border-radius: 20px;
  height: 20%;
  margin-left: 35%;
  margin-right: 35%;
  text-decoration: none !important; 
}
.pl-desc {
  margin: 35px 0px;
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 30px!important;
}
.pl-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.pl-list3 {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.pl-title1,.pl-title2,.pl-title3{
  text-decoration: underline;
}
.pl-list3 img {
  height: 40% !important;
  height: 40vh !important;
  margin-left: 50% !important;
    margin-right: 50% !important;
   
}
.pl-list3 img:hover {
 transform: translateX(-100%);
}
.pl-list img:hover {
 transform: translateY(-100%);
}
.pl-list img {
  margin-left: 25% !important;
  margin-right:25% !important;
 
}
@media screen and (max-width: 1000px)  {
  .pl-list img {
    margin-left: 0% !important;
    margin-right: 0% !important;
  }
  .pl-list3 img {
    margin-left: 10% !important;
    margin-right: 10% !important;
  }
}
@media screen and (max-width: 480px) {
  .pl-title4{
    animation: move2 4s ease-in-out infinite alternate;
    font-size: 15px;
  }
  @keyframes move2 {
    50%{
      transform: rotate(360deg);
    }
    100%{
      transform: rotate(0deg);
    }
}
}
@media screen and (max-width: 450px) {
  .hello {
    overflow: hidden;
    display: flex;
    width: 30% !important;
    height: 20% !important; 
  }
 
  .pl-list3 img {
    height: 25vh !important;
  }
}
@media screen and (max-width: 750px) {
  .hello { 
    margin-left: 10% !important;
  background-color:white;
    width: 30%;
    height: 30%;
    visibility: visible !important;
    margin: 20px 10px;
    display: flex;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px;
    background-color: white;
    overflow: hidden;
  }
  .hello img{
    padding: 1%;
    width: 100%;
    height: 100%;
    color: black;
    transition: all 10s ease;
    border-radius:15px !important;
  }
}
@media screen and (max-width: 480px) {
  .pl-texts {
    width: 100%;
  }
}
