.a {
  height: 100%;
  display: flex;
  align-items: center;
  background-color:rgb(242, 253, 225);
  /* width:100%; */
  /* margin-top: 20px !important; */
  /* margin-bottom: 100px !important; */
 
}
b{
  font-weight: bold;
  font-style: italic;
}
.a-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.a-right {
  flex: 1;
  width: 100% !important;
  margin-right: 2%;
  margin-left: 2%;
  /* margin-right: 0px !important ; */
  height: 100%;
  margin-bottom: 20px !important;
}

.a-card.bg {
  position: absolute;
  /* top: 50px; */
  /* left: 50px; */
  /* background-color: #333;*/
}

.a-card {
  margin-top: 20px !important;
  margin-bottom: 20px !important;
  width: 80%;
  height: 80vh;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
  /* padding: 10px; */
  /* box-shadow: 5px 10px 18px red; */
  box-shadow: 5px 12px 18px rgb(42, 95, 2);
}

.a-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
 
}

.a-title {
  font-weight: bolder;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
  margin-top:20px !important;
}
.a-title1 {
  font-weight: bolder;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  margin-top:20px !important;
}


.a-sub {
  margin: 10px 0px;
  font-size: 25px;
}

.a-desc{
    font-weight: 300;
    font-size: 17px;
}


.a-award{
    margin-top: 50px;
    display: flex;
    align-items: center;
    
    justify-content: space-between;
}

.a-award-texts{
  margin-top: 120px;
  margin-left: -121px;
}

.a-award-title{
    font-weight: bold;
    color: #555;
    font-size: 25px;
    margin-bottom: 10px;
}

@media screen and (max-width:700px) {
    .a{
      flex-direction: column;
      text-align: center;
      /* margin-top: 50px; */
    }

    .a-left {
      width: 100%;
    }

    .a-card {
      height: 50vh;
    }

    .a-card.bg, .a-award {
      display: none;
    }

    .a-right{
      padding: 0px;
    }
}